@media(max-width:1024px){
    .projects-section{
        display: flex;
        flex-direction: column;
        padding: 20px;
    
    }
    .projects-img{
        width:100%;
        border-radius: 32px;
        
    }
    .projects-heading{
        font-family: Roboto Mono;
        font-size: 20px;
        font-weight: 500;
        line-height: 16px;
        letter-spacing: 0em;
        text-align: left;
        align-self: center;
        color: #000000;
    
        
    }
    .projects{
        list-style-type: none;
        padding: 0px;
        display: flex;
        flex-direction: column;
        gap:20px;
    
    }
    .project-item{
        align-self: center;
    }
}

@media(min-width:1024px){
    .projects-section{
        display: flex;
        flex-direction: column;
        padding: 30px;
    
    }
    .projects-img{
        width:100%;
        border-radius: 32px;
        
    }
    .projects-heading{
        font-family: Roboto Mono;
        font-size: 20px;
        font-weight: 500;
        line-height: 16px;
        letter-spacing: 0em;
        text-align: left;
        
        color: #000000;
    
        
    }
    .projects{
        list-style-type: none;
        padding: 0px;
        display: flex;
        flex-wrap: wrap;
        
        gap:20px;
    }
    .project-item{
        align-self: center;
       
        
    }
}